// @flow
// import proj4 from 'proj4';
import {CanonicalTileID} from './tile_id';
import {Event, ErrorEvent, Evented} from '../util/evented';
import {getTiff, ResourceType} from '../util/ajax';
import EXTENT from '../data/extent';
import {RasterBoundsArray} from '../data/array_types';
import rasterBoundsAttributes from '../data/raster_bounds_attributes';
import SegmentVector from '../data/segment';
import Texture from '../render/texture';
import MercatorCoordinate from '../geo/mercator_coordinate';

import type {Source} from './source';
import type Map from '../ui/map';
import type Dispatcher from '../util/dispatcher';
import type Tile from './tile';
import type {Callback} from '../types/callback';
import type VertexBuffer from '../gl/vertex_buffer';
import type {
    TiffSourceSpecification,
} from '../style-spec/types';
import browser from '../util/browser';
import {getCoordinatesCenterTileID} from './image_source';
import {extend} from '../util/util';

type Coordinates = [[number, number], [number, number], [number, number], [number, number]];

/**
 * A data source containing an tiff.
 * (See the [Style Specification](https://www.mapbox.com/mapbox-gl-style-spec/#sources-tiff) for detailed documentation of options.)
 *
 * @example
 * // add to map
 * map.addSource('some id', {
 *    type: 'tiff',
 *    url: 'https://www.mapbox.com/images/foo.tiff',
 *    projection: 'EPSG:4326',
 * });
 *
 * // update url and coordinates simultaneously
 * mySource.updateImage({
 *    url: 'https://www.mapbox.com/images/bar.png',
 *    projection: 'EPSG:4326',
 * })
 *
 * map.removeSource('some id');  // remove
 * @see [Add an image](https://www.mapbox.com/mapbox-gl-js/example/image-on-a-map/)
 */
class TiffSource extends Evented implements Source {
  type: string;
  id: string;
  minzoom: number;
  maxzoom: number;
  tileSize: number;
  url: string | string[];

  tiles: {[_: string]: Tile};
  options: any;
  dispatcher: Dispatcher;
  map: Map;
  texture: Texture | null;
  georaster: any;
  tileID: CanonicalTileID;
  _boundsArray: RasterBoundsArray;
  boundsBuffer: VertexBuffer;
  boundsSegments: SegmentVector;
  _loaded: boolean;

  /**
   * @private
   */
  constructor(id: string, options: TiffSourceSpecification, dispatcher: Dispatcher, eventedParent: Evented) {
      super();
      this.id = id;
      this.dispatcher = dispatcher;

      this.type = 'tiff';
      this.minzoom = 0;
      this.maxzoom = 22;
      this.tileSize = 512;
      this.tiles = {};
      this._loaded = false;

      this.setEventedParent(eventedParent);

      this.options = options;
  }

  load(successCallback?: () => void) {
      this._loaded = false;
      this.fire(new Event('dataloading', {dataType: 'source'}));

      this.url = this.options.url;

      getTiff(Array.isArray(this.url) ? this.url.map((u) => this.map._requestManager.transformRequest(u, ResourceType.Tiff)) : this.map._requestManager.transformRequest(this.url, ResourceType.Tiff), (err, georaster) => {
          this._loaded = true;
          if (err) {
              this.fire(new ErrorEvent(err));
          } else if (georaster) {
              this.georaster = georaster;
              this.projection = proj4(this.options.projection || `EPSG:${this.georaster.projection}`, 'EPSG:4326');
              if (successCallback) {
                  successCallback();
              }
              this._finishLoading();
          }
      });
  }

  loaded(): boolean {
      return this._loaded;
  }

  /**
   * Updates the tiff URL and, optionally, the coordinates. To avoid having the image flash after changing,
   * set the `raster-fade-duration` paint property on the raster layer to 0.
   *
   * @param {Object} options Options object.
   * @param successCallback
   * @param {string} [options.url] Required tiff URL.
   * @returns {TiffSource} this
   */
  updateImage(options: {url: string}, successCallback?: () => void) {
      if (!this.georaster || !options.url) {
          if (successCallback) {
              successCallback(this);
          }
          return this;
      }
      this.options.url = options.url;
      this.load(() => {
          this.texture = null;

          if (this.timeUpdate) {
              this.lastTimeUpdate = this.timeUpdate;
          } else {
              this.lastTimeUpdate = this.timeAdded;
          }

          this.timeUpdate = browser.now();

          if (successCallback) {
              successCallback(this);
          }
      });
      return this;
  }

  _finishLoading() {
      if (this.map) {
          const [xmin, ymin, xmax, ymax] = [this.georaster.xmin, this.georaster.ymin, this.georaster.xmax, this.georaster.ymax];
          // top left, top right, bottom right, bottom left
          this.setCoordinates([
              this.projection.forward([xmin, ymax]),
              this.projection.forward([xmax, ymax]),
              this.projection.forward([xmax, ymin]),
              this.projection.forward([xmin, ymin]),
          ]);
          this.fire(new Event('data', {dataType: 'source', sourceDataType: 'metadata'}));
      }
  }

  onAdd(map: Map) {
      this.map = map;
      this.load(null, () => {
          this.timeAdded = browser.now();
      });
  }

  /**
   * Sets the image's coordinates and re-renders the map.
   *
   * @param {Array<Array<number>>} coordinates Four geographical coordinates,
   *   represented as arrays of longitude and latitude numbers, which define the corners of the image.
   *   The coordinates start at the top left corner of the image and proceed in clockwise order.
   *   They do not have to represent a rectangle.
   * @returns {TiffSource} this
   */
  setCoordinates(coordinates: Coordinates) {
      // Calculate which mercator tile is suitable for rendering the video in
      // and create a buffer with the corner coordinates. These coordinates
      // may be outside the tile, because raster tiles aren't clipped when rendering.

      // transform the geo coordinates into (zoom 0) tile space coordinates
      const cornerCoords = coordinates.map(MercatorCoordinate.fromLngLat);

      // Compute the coordinates of the tile we'll use to hold this image's
      // render data
      this.tileID = getCoordinatesCenterTileID(cornerCoords);

      // Constrain min/max zoom to our tile's zoom level in order to force
      // SourceCache to request this tile (no matter what the map's zoom
      // level)
      this.minzoom = this.maxzoom = this.tileID.z;

      // Transform the corner coordinates into the coordinate space of our
      // tile.
      const tileCoords = cornerCoords.map((coord) => this.tileID.getTilePoint(coord)._round());

      this._boundsArray = new RasterBoundsArray();
      this._boundsArray.emplaceBack(tileCoords[0].x, tileCoords[0].y, 0, 0);
      this._boundsArray.emplaceBack(tileCoords[1].x, tileCoords[1].y, EXTENT, 0);
      this._boundsArray.emplaceBack(tileCoords[3].x, tileCoords[3].y, 0, EXTENT);
      this._boundsArray.emplaceBack(tileCoords[2].x, tileCoords[2].y, EXTENT, EXTENT);

      if (this.boundsBuffer) {
          this.boundsBuffer.destroy();
          delete this.boundsBuffer;
      }

      this.fire(new Event('data', {dataType:'source', sourceDataType: 'content'}));
      return this;
  }

  createColorTexture(range = [], colors) {
      const interpolateColor = colors
          .map((item) => ({
              key: item[0],
              value: item[1],
          }));
      const keys = interpolateColor
          .map(d => parseFloat(d.key));
      const [min, max] = [range[0] || Math.min(...keys), range[1] || Math.max(...keys)];
      // eslint-disable-next-line no-undef
      const canvas = document.createElement('canvas');
      const ctx = canvas.getContext('2d');

      canvas.width = 256;
      canvas.height = 1;

      if (ctx) {
          if (this.options.colorScaleType !== 'linear') {
              const gradient = ctx.createLinearGradient(0, 0, 256, 0);

              for (let i = 0; i < interpolateColor.length; i += 1) {
                  const key = interpolateColor[i].key;
                  const color = interpolateColor[i].value;
                  gradient.addColorStop((key - min) / (max - min), color);
              }

              ctx.fillStyle = gradient;
              ctx.fillRect(0, 0, 256, 1);
          } else {
              for (let i = 0; i < interpolateColor.length; i += 1) {
                  const key = interpolateColor[i].key;
                  let keyNext = key;
                  if (i < interpolateColor.length - 1) {
                      keyNext = interpolateColor[i + 1].key;
                  } else {
                      keyNext = max;
                  }
                  const color = interpolateColor[i].value;
                  const current = (key - min) / (max - min) * 256; // 0 - 256
                  const next = (keyNext - min) / (max - min) * 256; // 0 - 256
                  ctx.fillStyle = color;
                  ctx.fillRect(current, 0, next - current, 1);
              }
          }

          return {
              canvas,
              colorRange: [min, max]
          };
      }
  }

  updateColorRamp(colorRange, colorRamp) {
      this.options = extend(this.options, {
          colorRange,
          colorRamp,
      });
      const colorConfig = this.createColorTexture(this.options.colorRange, this.options.colorRamp);
      this.colorRamp = colorConfig.canvas;
      this.colorRange = colorConfig.colorRange;
  }

  prepare() {
      if (Object.keys(this.tiles).length === 0 || !this.georaster) {
          return;
      }

      const context = this.map.painter.context;
      const gl = context.gl;

      if (!this.boundsBuffer) {
          this.boundsBuffer = context.createVertexBuffer(this._boundsArray, rasterBoundsAttributes.members);
      }

      if (!this.boundsSegments) {
          this.boundsSegments = SegmentVector.simpleSegment(0, 0, 4, 2);
      }

      this.textureSize = [this.georaster.width, this.georaster.height];

      if (!this.texture) {
          // const len = this.textureSize[0] * this.textureSize[1] * 4;
          // const data = new Uint8Array(len);
          // for (let y = 0; y < this.textureSize[1]; y++) {
          //     for (let x = 0; x < this.textureSize[0]; x++) {
          //         const k = y * this.textureSize[0] + x;
          //         const i = k * 4;
          //         const rgba = encodeFloat(this.georaster.rasters[0][k]);
          //         // console.log(k);
          //         // data[i + 0] = this.georaster.rasters[0][k] - 127;
          //         // data[i + 1] = this.georaster.rasters[1][k] - 127;
          //         // data[i + 2] = this.georaster.rasters[2][k] - 127;
          //         // data[i + 3] = this.georaster.rasters[3][k] - 127;
          //         data[i + 0] = rgba[0];
          //         data[i + 1] = rgba[1];
          //         data[i + 2] = rgba[2];
          //         data[i + 3] = rgba[3];
          //     }
          // }
          //
          // for (let j = 0; j < this.georaster.rasters[0].length; j++) {
          //     const item1 = this.georaster.rasters[0][j];
          //     const item2 = decodeFloat(data[j * 4 + 0], data[j * 4 + 1], data[j * 4 + 2], data[j * 4 + 3]);
          //     if (item1 - item2 > 0.0000001) {
          //         console.log(item1, item2);
          //     }
          // }

          this.texture = new Texture(context, {
              width: this.textureSize[0],
              height: this.textureSize[1],
              data: new Float32Array(this.georaster.rasters[0]),
          }, gl.LUMINANCE, {
              dataType: gl.FLOAT,
          });
          this.texture.bind(gl.LINEAR, gl.CLAMP_TO_EDGE);
      }
      this.dataRange = this.options.dataRange !== undefined ? this.options.dataRange : [this.georaster.min, this.georaster.max];
      this.nodata = this.options.noData !== undefined ? this.options.noData : (this.georaster.noDataValue || 0);

      for (const w in this.tiles) {
          const tile = this.tiles[w];
          if (tile.state !== 'loaded') {
              tile.state = 'loaded';
              tile.texture = this.texture;
              if (tile.timeUpdate) {
                  tile.lastTimeUpdate = tile.timeUpdate;
              } else {
                  tile.lastTimeUpdate = tile.timeAdded;
              }
              tile.timeUpdate = browser.now();
          }
      }
  }

  getGeoJSONData() {
      const features: any = {
          type: 'FeatureCollection',
          features: [],
      };
      for (let j = this.georaster.height; j >= 0; j--) {
          for (let i = 0; i < this.georaster.width; i++) {
              const value = this.queryValueByIndex(i, j);
              const mc = [i * this.georaster.pixelWidth + this.georaster.xmin, this.georaster.ymax - j * this.georaster.pixelHeight];
              const coordinates = this.projection.forward(mc);

              features.features.push({
                  type: 'Feature',
                  properties: {
                      value,
                  },
                  geometry: {
                      type: 'Point',
                      coordinates,
                  },
              });
          }
      }

      return features;
  }

  loadTile(tile: Tile, callback: Callback<void>) {
      // We have a single tile -- whoose coordinates are this.tileID -- that
      // covers the tiff we want to render.  If that's the one being
      // requested, set it up with the tiff; otherwise, mark the tile as
      // `errored` to indicate that we have no data for it.
      // If the world wraps, we may have multiple "wrapped" copies of the
      // single tile.
      if (this.tileID && this.tileID.equals(tile.tileID.canonical)) {
          this.tiles[String(tile.tileID.wrap)] = tile;
          tile.buckets = {};
          callback(null);
      } else {
          tile.state = 'errored';
          callback(null);
      }
  }

  serialize(): Object {
      return {
          type: 'tiff',
          url: this.options.url,
      };
  }

  bilinearInterpolateVector(
    x: number, y: number,
    g00,
    g10,
    g01,
    g11,
  ) {
      const rx = 1 - x;
      const ry = 1 - y;

      const a = rx * ry;
      const b = x * ry;
      const c = rx * y;
      const d = x * y;
      return g00 * a + g10 * b + g01 * c + g11 * d;
  }

  queryValueByIndex(x, y) {
      try {
          // iterate through the bands
          // get the row and then the column of the pixel that you want
          if (x >= 0 && x < this.georaster.width && y >= 0 && y < this.georaster.height) {
              if (this.georaster.values) {
                  // loaded completely into memory
                  return [
                      this.georaster.values.map(rows => rows[y][x]),
                      this.georaster.values.map(rows => rows[y][x + 1 >= this.georaster.width ? x : x + 1]),
                      this.georaster.values.map(rows => rows[y + 1 >= this.georaster.height ? y : y + 1][x]),
                      this.georaster.values.map(rows => rows[y + 1 >= this.georaster.height ? y : y + 1][x + 1 >= this.georaster.width ? x : x + 1]),
                  ];
              } else {
                  return this.georaster.getValues({height: 1, width: 1, left: x, top: y, right: x + 1, bottom: y + 1}).then(bands => bands.map(band => band[0][0]));
              }
          } else {
              return null;
          }
      } catch (e) {
          console.error(e);
          throw e;
      }
  }

  queryValue(coordinates) {
      // The convertGeometry function takes the input
      // geometry and converts it to a standard format.
      const [xInCrs, yInCrs] = this.projection.inverse(coordinates);

      // By normalizing the difference in latitude and longitude between the image
      // origin and the point geometry by the cell height and width respectively,
      // we can map the latitude and longitude of the point geometry in the
      // coordinate space to their associated pixel location in the image space.
      // Note that the y value is inverted to account for the inversion between the
      // coordinate and image spaces.
      const x = Math.floor((xInCrs - this.georaster.xmin) / this.georaster.pixelWidth);
      const y = Math.floor((this.georaster.ymax - yInCrs) / this.georaster.pixelHeight);
      const array = this.queryValueByIndex(x, y);
      const xj = (xInCrs - this.georaster.xmin) / (this.georaster.xmax - this.georaster.xmin);
      const yj = (this.georaster.ymax - yInCrs) / (this.georaster.ymax - this.georaster.ymin);
      // const fi = ((this.georaster.width * xj) % 1.0);
      // const fj = (this.georaster.height * yj) % 1.0;
      if (!array || array[0][0] <= this.nodata || array[1][0] <= this.nodata || array[2][0] <= this.nodata || array[3][0] <= this.nodata) {
          return this.nodata;
      }
      return this.bilinearInterpolateVector(xj, yj, array[0][0], array[1][0], array[2][0], array[3][0]);
  }

  hasTransition() {
      return false;
  }
}

export default TiffSource;
